@import url(https://fonts.googleapis.com/css?family=Heebo);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*start general style*/
* {
  -o-box-sizing: border-box;
  box-sizing: border-box;
  list-style-type: none;
  padding: 0;
  font-family: 'Heebo', sans-serif;
}
/*end general style*/

.container {
  display: -webkit-flex;
  display: flex;
}
/*side bar style*/
.container .sidebar {
  -webkit-order: 1;
          order: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  max-height: 100%;
  max-width: 17%;
  background: linear-gradient(to bottom, #3b679e 0%,#2b88d9 50%,#207cca 51%,#7db9e8 100%);
  color: #fff;
  cursor: pointer;
}
/*body style*/
.container .body {
  -webkit-order: 2;
          order: 2;
  -webkit-flex-grow: 10;
          flex-grow: 10;
  max-height: 100%;
  background-color: #eee;
  max-width: 83%;
}

/*responsive*/

@media screen and (min-width: 767px) and (max-width: 1025px) {
  .container .body {
    max-width: 68%;
  }

  .container .sidebar {
    max-width: 33%;
  }

  .exams-time .exams-content button {
    margin: 5% 0 35% 0%;
    width: 90%;
  }
}

@media screen and (min-width: 416px) {
  .collapse {
    display: none;
  }
}

@media screen and (max-width: 415px) {
  .container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .collapse {
    display: block;
    margin-top: 5%;
  }

  .hide {
    display: none;
  }

  .container .sidebar {
    max-width: 100%;
  }

  .sidebar .logo {
    font-weight: bold;
    font-size: 1.5em;
  }

  .container .body {
    max-width: 100%;
  }

  /*header style*/

  header {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  header .user {
    margin: auto;
  }

  header .search input {
    width: 100%;
  }

  header .res-order {
    bottom: 30%;
  }

  header .user-photo {
    margin-left: 54%;
    max-width: 12%;
    margin-bottom: 5%;
  }

  /*announcement*/

  .content-container .announcement {
    -webkit-flex: 1 1 100%;
            flex: 1 1 100%;
  }

  .announcement-content .info-data {
    padding-right: 3%;
  }

  /*exams*/

  .exams-time .exams-content button {
    margin: 5% 25% 15%;
    width: 50%;
  }

}

header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: #fff;
}

.user {
    -webkit-order: 1;
            order: 1;
    margin: 2.5%;
}

.info {
    -webkit-order: 2;
            order: 2;  
}

.search, input {
    position: relative;
}

.search::after {
    content: attr(data-pic);
    position: absolute;
    top: 2px;
    right: 10px;
    font-family: 'FontAwesome';
    font-size: 20px;
    display: block;
    color: rgba(0, 0, 0, 0.6);
    font-weight: bold;
}

.search input::-webkit-input-placeholder {
    padding-left: 3%;
    font-size: 15px;
}

.search input {
    width: 65%;
    height: 50%;
    border-radius: 20px;
}

.info .bell {
    margin-right: 5%;
    margin-left: 3%;
    color: #207cca;
}

.info .envelope {
    color: #207cca;
}

.user-photo {
    max-width: 7%;
    border-radius: 50%;
    margin-left: 3%;
    margin-top: 2%;
}


*.icon-blue {color: #0088cc}
*.icon-grey {color: grey}
header span {   
    width:100px;
    text-align:center;
    vertical-align:middle;
    position: relative;
}
.badge:after{
    content: "3";
    position: absolute;
    background: #7db9e8;
    height: 1.5em;
    bottom: 80%;
    left: 51%;
    width: 1.6em;
    text-align: center;
    font-size: 1rem;
    border-radius: 50%;
    color: white;
    border: 1px solid #7db9e8;
}
.content-container {
    margin: 2%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

/*start exams time section*/

.exams-time {
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    width: 100%
}

.exams-time .exams-content {
    padding-left: 3%;
}

.exams-content h1 {
    color: #207cca;
    font-size: 1.5em;
}

.exams-content q,
.exams-content span {
    font-style: italic;
    color: #7a7c7e
}

.exams-content button {
    display: block;
    margin-top: 2%;
    background-color: #2d9bfc;
    color: #fff;
    width: 25%;
    height: 15%;
    border-radius: 10px;
    font-size: 1.2em;
    border: 1px solid #2d9bfc;
    cursor: pointer;
}

.exams-content button:hover {
    background-color: #fff;
    color: #2d9bfc;
}

/*end exams time section*/

/*start announcement section*/

.announcement {
    background-color: #fff;
    position: relative;
    border-radius: 10px;
    -webkit-flex: 2 1 60%;
            flex: 2 1 60%;
    margin: 2% 2% 0 0;
}

.announcement .announcement-head {
    padding-left: 3%;
}

.announcement-head h4 {
    margin-bottom: 0;
    color: #4d4e4e;
}

.announcement-head span {
    color: #7a7c7e;
}

.announcement .all-announcement {
    position: absolute;
    top: 3%;
    right: 4%;
    color: #2d9bfc;
    font-weight: bold
}

.announcement-content {
    padding: 3%;
    display: -webkit-flex;
    display: flex;
}

.info-data {
    padding-right: 2%;
}

.info-data .name {
    color: #4f5252;
    font-weight: bold;
}

.info-data .title {
    color: #7a7c7e;
}

.info-data img {
    max-width: 50%;
    border-radius: 50%;
}

.announcement-content .content {
    color: #7a7c7e;
}

/*end announcement section*/

/*start whats due section*/

.whats-due {
    background-color: #fff;
    -webkit-flex: 3 1 20%;
            flex: 3 1 20%;
    margin-top: 2%;
    border-radius: 10px;
}

.whats-due-head {
    position: relative;
    padding-left: 3%;
}

.whats-due-head h4 {
    margin-bottom: 0;
    color: #4d4e4e;
}

.all-whats-due {
    position: absolute;
    right: 8%;
    top: 1%;
    color: #2d9bfc;
    font-weight: bold
}

.whats-due-head .quote {
    color: #7a7c7e;
}

.whats-due-content {
    margin-top: 5%;
    padding: 3%;
    color: #4d4e4e;
}

.whats-due-content .info {
    color: #7a7c7e;
}

.whats-due-content .item-icon {
    color: #2d9bfc;
}

.whats-due-content button {
    background-color: transparent;
    color: #2d9bfc;
    width: 100%;
    border-radius: 10px;
    padding: 10px;
    border: 1px solid #2d9bfc;
    cursor: pointer;
    font-weight: bold;
}

.whats-due-content button:hover {
    color: #fff;
    background-color: #2d9bfc;
}

/*end whats due section*/
.logo {
    text-align: center;
    font-size: 1.3em;
}

.menu {
    font-size: 1.2em;
    padding-top: 15px;
}

.menu .menu-item  {
    padding: 1.3em;
    cursor: pointer;
}

.menu .menu-item:hover {
    background-color: #fff;
    color: #3b679e;
    border-left: 5px solid #207cca;
}

.menu .menu-item .item-icon {
    padding-right: 1.4em;
}
